import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import firebase from "firebase";
import VueToastr2 from "vue-toastr-2";
import axios from "axios";
import VueAxios from "vue-axios";
import VueFire from "vuefire";
import VueClipboard from "vue-clipboard2";

window.toastr = require("toastr");
import "vue-toastr-2/dist/vue-toastr-2.min.css";

Vue.use(VueToastr2);
Vue.use(VueAxios, axios);
Vue.use(VueFire);
VueClipboard.config.autoSetContainer = true; // add this line
Vue.use(VueClipboard);

const config = {
  apiKey: "AIzaSyD-GkjsMg-tcslZqSQi8gq4vYDPr08HQ7s",
  authDomain: "thuongupload.firebaseapp.com",
  databaseURL: "https://thuongupload.firebaseio.com",
  projectId: "thuongupload",
  storageBucket: "thuongupload.appspot.com",
  messagingSenderId: "111368187517",
  appId: "1:111368187517:web:b5661f483380a8c4"
};

firebase.initializeApp(config);

firebase.auth().onAuthStateChanged(function(user) {
  if(!user) return;
  debugger
  const db = firebase.database();
  store.commit("setData", { db, firebase });
});

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App),
  methods: {
    showError(message) {
      return this.$toastr.error(message, "Somthing went wrong !", {
        closeButton: false,
        debug: false,
        newestOnTop: false,
        progressBar: false,
        positionClass: "toast-bottom-right",
        preventDuplicates: false,
        onclick: null,
        showDuration: "300",
        hideDuration: "3000",
        timeOut: "5000",
        extendedTimeOut: "1000",
        showEasing: "swing",
        hideEasing: "linear",
        showMethod: "fadeIn",
        hideMethod: "fadeOut"
      });
    },
    showSuccess(message) {
      return this.$toastr.success(message, "operation success !", {
        closeButton: false,
        debug: false,
        newestOnTop: false,
        progressBar: false,
        positionClass: "toast-bottom-right",
        preventDuplicates: false,
        onclick: null,
        showDuration: "300",
        hideDuration: "3000",
        timeOut: "5000",
        extendedTimeOut: "1000",
        showEasing: "swing",
        hideEasing: "linear",
        showMethod: "fadeIn",
        hideMethod: "fadeOut"
      });
    }
  }
}).$mount("#app");
