<script>
import svgico from "@/svgico.vue";
import PulseLoader from "vue-spinner/src/PulseLoader.vue";
import firebase from "firebase";


export default {
  components: {
    PulseLoader,
    svgico
  },
  data() {
    return {
      file: null,
      uploading: false,
      fileUploaded: [],
      selectMH: null,
      titelDoc: "",
      passfile: "",
      resultUpload: null,
      tailieu: null,
      uid: null,
      user: null,
      modalUser: "",
      loadingLogin: false,
      binding: false
    };
  },
  computed: {
    getDB() {
      return this.$store.state.db;
    },
    getList() {
      const result = this.fileUploaded;
      debugger
      return result.sort((a, b) => {
        return b.timestamp - a.timestamp;
      });
    }
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"));
  },
  methods: {
    onCopy(){
      this.$root.showSuccess("Đã copy link download to clipboard");
    },
    async onDelete(fileid){
      try{
        const filesnap = await this.$store.state.db.ref("files/"+fileid).once("value");
        const file = filesnap.val();
        await this.$store.state.firebase
          .storage().ref(file.meta.fullPath).delete();
        await this.$store.state.db.ref("files/"+fileid).remove();
        this.$root.showSuccess("Đã xóa file");

      }catch(ex){

      }

      debugger
    },
    bindingData(db) {
      if (this.binding || !db) {
        return;
      }
      this.$bindAsArray("fileUploaded", db.ref("files/"));

      this.binding = true;
    },
    logOut() {
      this.user = null;
      this.modalUser = null;
      localStorage.setItem("user", null);
      window.location.href = "/";
    },
    async handleLogin() {
      try {
        this.loadingLogin = true;
        const { data } = await this.axios.get(
          "https://asia-northeast1-thuongupload.cloudfunctions.net/api/login/"+this.modalUser,
        );

        if (data.error) {
          this.loadingLogin = false;
          return this.$root.showError(data.error);
        } else {
          await firebase.auth().signInWithCustomToken(data.token);
          this.loadingLogin = false;
          this.user = data;
          localStorage.setItem("user", JSON.stringify(data));
        }
      } catch (error) {
        console.log(error);
        this.loadingLogin = false;
        return this.$root.showError(error);
      }
    },
    downloadFile(file) {
      if (file.password) {
        const password = prompt("Nhập vào mật khẩu để tải file", "");
        if (password != file.password) {
          return this.$root.showError("Mật khẩu không đúng");
        }
      }
      const url = file.url;
      const link = document.createElement("a");
      link.href = url;
      link.target = "_blank";
      link.click();
    },
    async invisiableDoc(id, maLop) {
      try {
        const db = this.$store.state.db;
        await db.ref("tailieu/" + maLop + "/" + id).update({
          visible: false
        });
      } catch (error) {
        return this.$root.showError(error.toString());
      }
    },
    async handleSubmitFile() {
      try {
        if (!this.titelDoc) {
          throw new Error("Bạn phải nhập tiêu đề cho tài liệu.");
        }

        const firebase = this.$store.state.firebase;
        const db = this.$store.state.db;

        const obj = {
          ...this.resultUpload,
          timestamp: firebase.database.ServerValue.TIMESTAMP,
          visible: true,
          name: this.titelDoc
        };

        if (this.passfile) {
          obj["password"] = this.passfile;
        }
        debugger

        await db.ref("files").push(obj);
        this.$root.showSuccess("Tài liệu của bạn được upload thành công.");

        this.file = null;
        this.uploading = false;
        this.titelDoc = "";
        this.resultUpload = null;
        return;
      } catch (error) {
        return this.$root.showError(error.toString());
      }
    },
    trigerFile() {
      document.querySelector("input[type=file]").click();
    },
    async handleFileSelect(evt) {
      this.file = evt.target.files[0]; // FileList object
      try {
        this.resultUpload = await this.uploadFile(
          `/files/${Math.random()
            .toString(36)
            .substring(7)}/` + this.file.name,
          this.file
        );
        this.titelDoc = this.file.name;
        this.uploading = true;
        document.querySelector("input[type=file]").value = "";
      } catch {
        this.$root.showError("Upload thất bại. Lưu ý max size upload là 20mb");
        this.file = null;
        document.querySelector("input[type=file]").value = "";
      }
    },
    async uploadFile(filePath, file) {
      return this.$store.state.firebase
        .storage()
        .ref(filePath)
        .put(file)
        .then(async function(fileSnapshot) {
          const [url, meta] = await Promise.all([
            fileSnapshot.ref.getDownloadURL(),
            fileSnapshot.ref.getMetadata()
          ]);
          return {
            url,
            meta: {
              size: meta.size,
              name: meta.name,
              updated: meta.updated,
              fullPath: meta.fullPath,
              bucket: meta.bucket
            }
          };
        });
    }
  },
  mounted() {
    const $ = document.querySelector.bind(document);
    const App = {};
    App.init = (function() {
      $("#drop").ondragleave = function(evt) {
        $("#drop").classList.remove("active");
        evt.preventDefault();
      };
      $("#drop").ondragover = $("#drop").ondragenter = function(evt) {
        $("#drop").classList.add("active");
        evt.preventDefault();
      };
      $("#drop").ondrop = function(evt) {
        $("input[type=file]").files = evt.dataTransfer.files;
        $("footer").classList.add("hasFiles");
        $("#drop").classList.remove("active");
        evt.preventDefault();
      };
    })();
  },
  watch: {
    getDB: function(val) {
      if (!val || !this.user) {
        // binding data
        return;
      }
      this.bindingData(val);
    },
    user: function(val) {
      if (!val || !this.getDB) {
        // binding data
        return;
      }
      this.bindingData(this.getDB);
    }
  }
};
</script>
